import standard from './standard.json' assert { type: 'json' }

export class Mime {
  #types = Object.create(null)
  #extensions = Object.create(null)

  constructor (...args) {
    for (let i = 0; i < args.length; i++) {
      this.define(args[i])
    }
  }

  /**
   * Define mimetype -> extension mappings.  Each key is a mime-type that maps
   * to an array of extensions associated with the type.  The first extension is
   * used as the default extension for the type.
   *
   * e.g. mime.define({'audio/ogg', ['oga', 'ogg', 'spx']});
   *
   * If a type declares an extension that has already been defined, an error will
   * be thrown.  To suppress this error and force the extension to be associated
   * with the new type, pass `force`=true.  Alternatively, you may prefix the
   * extension with "*" to map the type to extension, without mapping the
   * extension to the type.
   *
   * e.g. mime.define({'audio/wav', ['wav']}, {'audio/x-wav', ['*wav']});
   *
   *
   * @param {Object} typeMap type definitions
   * @param {boolean} [force] if true, force overriding of existing definitions
   */
  define (typeMap, force) {
    for (let type in typeMap) {
      const extensions = typeMap[type].map(t => t.toLowerCase())
      type = type.toLowerCase()

      for (let i = 0; i < extensions.length; i++) {
        const ext = extensions[i]

        // '*' prefix = not the preferred type for this extension.  So fixup the
        // extension, and skip it.
        if (ext[0] === '*') continue

        if (!force && (ext in this.#types)) {
          throw new Error(
            'Attempt to change mapping for "' + ext +
            '" extension from "' + this.#types[ext] + '" to "' + type +
            '". Pass `force=true` to allow this, otherwise remove "' + ext +
            '" from the list of extensions for "' + type + '".'
          )
        }

        this.#types[ext] = type
      }

      // Use first extension as default
      if (force || !this.#extensions[type]) {
        const ext = extensions[0]
        this.#extensions[type] = (ext[0] !== '*') ? ext : ext.substr(1)
      }
    }
  }

  /**
   * Lookup a mime type based on extension
   * @param {string} path
   */
  getType (path) {
    const last = path.replace(/^.*[/\\]/, '').toLowerCase()
    const ext = last.replace(/^.*\./, '').toLowerCase()

    const hasPath = last.length < path.length
    const hasDot = ext.length < last.length - 1

    return (hasDot || !hasPath) && this.#types[ext]
  }

  /**
   * Return file extension associated with a mime type
   * @param {string} type
   */
  getExtension (type) {
    type = /^\s*([^;\s]*)/.test(type) && RegExp.$1
    return type && this.#extensions[type.toLowerCase()]
  }
}

export default new Mime(standard)